body:not(.block-editor-page) .card-hover {
  width: 360px;
  height: 500px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 32px -10px rgba(0, 0, 0, 0.08);
}
body:not(.block-editor-page) .card-hover:hover .card-content {
  background-color: #DBDBDB;
  color: #000;
  bottom: 100%;
  transform: translateY(100%);
  padding: 50px 60px;
  transition: all 0.35s cubic-bezier(0.1, 0.72, 0.4, 0.97);
}
body:not(.block-editor-page) .card-hover:hover a {
  opacity: 1;
  transform: translate(-50%, 0);
  transition: all 0.3s 0.35s cubic-bezier(0.1, 0.72, 0.4, 0.97);
}
body:not(.block-editor-page) .card-hover .card-content {
  text-align: center;
  background-color: #313131;
  color: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(0);
  transition: all 0.35s 0.35s cubic-bezier(0.1, 0.72, 0.4, 0.97);
  will-change: bottom, background-color, transform;
  z-index: 1;
}
body:not(.block-editor-page) .card-hover .card-content::before, body:not(.block-editor-page) .card-hover .card-content::after {
  content: "";
  width: 100%;
  height: 120px;
  background-color: inherit;
  position: absolute;
  left: 0;
  z-index: -1;
}
body:not(.block-editor-page) .card-hover .card-content::before {
  top: -80px;
  clip-path: ellipse(60% 80px at bottom center);
}
body:not(.block-editor-page) .card-hover .card-content::after {
  bottom: -80px;
  clip-path: ellipse(60% 80px at top center);
}
body:not(.block-editor-page) .card-hover h3 {
  font-size: 1.5rem;
  margin-bottom: 1em;
}
body:not(.block-editor-page) .card-hover a {
  opacity: 0;
}
body:not(.block-editor-page) .card-hover i {
  width: 18px;
  margin-left: 4px;
  transition: transform 0.3s;
}
body:not(.block-editor-page) img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

